.main-footer{
  padding-bottom: 80px;
  &__item{
    @media screen and (max-width: @sm-width ) {
      margin-bottom: 20px;
    }
  }
  &__title{
    margin: 0;
    margin-bottom: 15px;
    font-size: 10px;
    line-height: 24px;
    text-transform: uppercase;
    @media screen and (max-width: @sm-width ) {
      margin-bottom: 0;
    }
  }
  &__nav-section{
    .inline-block;
    width: 60px;
    margin-right: 15px;
    &:last-of-type{
      margin-right: 0;
    }
  }
  &__nav-item{
    .inline-block;
    margin-bottom: 5px;
    color: @text-color;
    &:hover{
      color: @text-color;
    }
  }
  &__social-block{
    width: 120px;
    .icon{
      margin: 6px;
      cursor: pointer;
    }
  }
  &__newsletter{
    p{
      margin-bottom: 10px;
    }
  }
  &__button{
    position: relative;
    width: 170px;
    font-size: 12px;
    font-weight: 200;
    text-align: left;
    .line-height(25px);
    background: @bg-gray;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all .8s;
    &:hover{
      background: darken(@bg-gray, 10%);
    }
    .icon{
      position: absolute;
      top: 7px;
      right: 10px;
    }

  }
  &__about{

  }
}