.main-slider{
  margin-bottom: 40px;
  &__item{
    position: relative;
    height: 300px;
    overflow: hidden;
    @media screen and (min-width: @md-width ) {
      img{
        width: 100%;
      }
    }
  }
  &__caption{
    position: absolute;
    bottom: 5%;
    left: 5%;
    max-width: 300px;
    height: 75px;
    z-index: 10;
    .custom-tooltip{
      top: -20px;
      left: 0;
      z-index: 1001;
    }
    &-title{
      padding-left: 10px;
      font-size: 20px;
      font-weight: 200;
      line-height: 25px;
      color: @white;
      text-transform: uppercase;
      background: @bg-blue;
    }
    &-content{
      padding: 10px;
      font-size: 14px;
      font-weight: 200;
      line-height: 19px;
      color: @text-color;
      background: @white;
    }
  }
}