.articles{
  &__item{
    position: relative;
    margin-bottom: 30px;
    height: auto;
    @media screen and (max-width: @sm-width ) {
      float: none;
      margin: 0 auto;
      margin-bottom: 20px;
      max-width: 400px;
      height: auto;
    }
  }
  &__img{
    margin-bottom: 5px;
    width: 100%;
    height: 170px;
    overflow: hidden;
  }
  &__caption{
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
  &__date{
    margin-bottom: 10px;
  }
  .icon{
    margin-top: -3px;
    margin-right: 5px;
  }
  &__content{
    font-size: 13px;
    line-height: 18px;
  }
  .read-more-text{
    display: none;
  }
  .btn__blue{
    margin-top: 10px;
  }
}