}
@media screen and (min-width: 960px) {
  .header-bottom .main-menu {
    display: block !important;
  }
}
.header-bottom .main-menu__item {
  display: inline-block;
  vertical-align: middle;
  margin-right: -4px;
  padding: 0 10px;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  text-transform: uppercase;
  color: #717171;
  transition: all .5s;
}
.header-bottom .main-menu__item.active,
.header-bottom .main-menu__item:hover {
  text-decoration: none;
  color: #fff;
  background: #d45757;
