
body{
  font-family: "Open Sans", sans-serif;
  font-size: @regular;
  line-height: @line-height;
  font-weight: 400;
  color: @text-color;
  background: @white;
}
.clearfix::after{
  content: '';
  display: block;
  clear: both;
}
.text-red{
  color: red;
}
i.icon{
  .inline-block;
}
a.btn{
  .inline-block;
  padding: 0 10px;
  height: 27px;
  font-size: 13px;
  line-height: 27px;
  text-decoration: none;
  &:hover{
    text-decoration: none;
  }
}
a.btn__blue{
  color: @white;
  background: @bg-blue;
  transition: all .5s;
  &:hover{
    background: darken(@bg-blue, 10%);
  }
}
.custom-tooltip{
  position: absolute;
  top: -5px;
  left: 20px;
  padding: 0 10px;
  .line-height(20px);
  font-size: 11px;
  text-transform: uppercase;
  color: @white;
  background: @black;
}