.inline-block(@valign: middle) {
  display: inline-block;
  vertical-align: @valign;
}

.line-height(@px) {
  height: @px;
  line-height: @px;
}

/*grid
=================================*/
.grid-universe(@name, @count, @i: 1) when (@i <= @count){
  .@{name}-@{i}{
    width: @i * (100% / @count);
  }
  .grid-universe(@name, @count, @i+1);
}


