.featured{
  padding-bottom: 25px;
  border-bottom: 1px solid @border-color;
  &__title{
    margin-bottom: 20px;
    padding-left: 10px;
    font-family: Verdana, sans-serif;
    font-size: 16px;
    .line-height(30px);
    text-transform: uppercase;
    color: @black;
    background: @bg-gray;
  }
  &__item{
    margin-bottom: 15px;
  }
  &__img{
    width: 120px;
    height: 100px;
    overflow: hidden;
    @media screen and (max-width: @md-width ) {
      margin-bottom: 10px;
    }
    &.col-xs-6{
      padding-left: 15px;
      padding-right: 5px;
    }
  }
  &__content.col-xs-6{
    padding: 0;
    padding-left: 5px;
    width: 62%;
  }
  &__content-title{
    font-size: 14px;
    line-height: 20px;
  }
}