}

/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  top: 90%;
  width: 15px;
  height: 20px;
  font-size: 0;
  color: transparent;
  background-image: url("../img/sprite.png");
  background-color: transparent;
  border: none;
  outline: none;
  opacity: .6;
  z-index: 1;
}
.slick-prev:focus,
.slick-prev:hover,
.slick-next:hover,
.slick-next:focus{
  opacity:1;
}


.slick-prev {
  right: 35px;
  background-position: -33px -32px;
}

.slick-next {
  right: 15px;
  background-position: 0 -62px;
}

/* Dots */

.slick-dotted .slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
  li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
    button {
      border: 0;
      background: transparent;
      display: block;
      height: 20px;
      width: 20px;
      outline: none;
      line-height: 0;
      font-size: 0;
      color: transparent;
      padding: 5px;
      cursor: pointer;
      &:hover, &:focus {
        outline: none;
        &:before {
          opacity: @slick-opacity-on-hover;
        }
      }
      &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: @slick-dot-character;
        width: 20px;
        height: 20px;
        font-family: @slick-font-family;
        font-size: @slick-dot-size;
        line-height: 20px;
        text-align: center;
        color: @slick-dot-color;
        opacity: @slick-opacity-not-active;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
    }
    &.slick-active button:before {
      color: @slick-dot-color-active;
      opacity: @slick-opacity-default;
    }
  }
}
